.button__with-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.6rem;
    border: 1px solid;
    border-radius: 10px;
    padding: 0.3rem 0.6rem;
}

#HOMEPAGE {
    // background-color: grey;
    height: 100%;
    display: flex;
    flex-direction: column;

    .title {
        align-self: center;
        margin-bottom: 0.3rem;
        font-size: 0.9rem;
    }

    .videoplayer {
        height: 28em;
        margin-bottom: 1rem;
    }

    .notice {
        font-size: 0.9rem;
        color: #ff6666;

        .browser__warn {
            font-size: 1.2rem;
        }

        .notice__title {
            font-size: 1.2rem;
            margin-right: 0.5rem;
        }
    }
}

@media screen and (max-width: 786px) {
    #HOMEPAGE {
        .videoplayer {
            height: 20rem;
        }
    }
}
