#NAVBAR {
    position: relative;
    display: flex;
    align-items: center;
    gap: 1.5rem;
    margin-top: 0.3rem;

    .logo {
        cursor: pointer;
        margin-right: auto;
    }

    .contact,
    .legal {
        span {
            margin-top: 1rem;
            cursor: pointer;
            &:hover {
                color: #ff6666;
            }
        }
    }
}
