#POPUP {
    position: absolute;
    top: 6rem;
    left: 0;
    // right: 0;
    z-index: 100;
    height: 78vh;
    width: 100%;

    border: 3px solid #d4d4d4;
    border-radius: 20px;
    display: flex;
    gap: 1rem;
    flex-direction: column;
    align-items: center;
    padding: 1rem;
    overflow-y: scroll;
    background-color: rgba($color: #000000, $alpha: 0.5);
    -webkit-backdrop-filter: blur(18px);
    backdrop-filter: blur(18px);

    .button__close {
        color: #000000;
        position: -webkit-sticky;
        position: sticky;
        top: 0;
        padding: 0.2rem 0.8rem;
        margin-bottom: 1rem;
        border: 2px solid #d4d4d4;
        background-color: #ff6666;
        border-radius: 20px;
        cursor: pointer;
        &:active {
            color: #ffffff;
        }
    }
}

@media screen and (max-width: 786px) {
    #POPUP {
        margin-left: 0.4rem;
    }
}
