#VIDEO {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;

    iframe {
        border-radius: 20px;
    }

    .options {
        display: flex;
        justify-content: space-between;
        margin-top: 1rem;

        .option__server {
            position: relative;

            .servers {
                position: absolute;
                width: 100%;
                padding: 1rem;
                display: flex;
                flex-direction: column;
                gap: 1rem;
                border: 1px solid;
                border-radius: 20px;
                bottom: 2.8rem;
                -webkit-backdrop-filter: blur(10px);
                backdrop-filter: blur(10px);

                div {
                    cursor: pointer;
                    &:hover {
                        color: #666666;
                    }
                }
            }
        }

        .refresh,
        .server__select {
            cursor: pointer;

            &:active {
                background-color: #ff6666;
                color: black;
            }
        }
    }
}

.hide {
    display: none !important;
}

.active {
    color: #ff6666;
}

/* ---------- loading --------- */
.loading {
    position: absolute;
    top: 40%;
    left: 0;
    right: 0;
    margin: 0 auto;
    /* Size */
    height: 3rem;
    width: 3rem;

    /* Create a curve at the top */
    border: 4px solid #ffffff;
    border-top-color: #ff6666;
    border-radius: 50%;
    animation: spinner 800ms linear infinite;

    @keyframes spinner {
        from {
            transform: rotate(0deg);
        }
        to {
            transform: rotate(360deg);
        }
    }
}
/* -------- end loading -------- */
