#APP {
    position: relative;
    margin: 0.5rem 2rem 1rem;
    display: grid;
    grid-template-areas:
        "navbar navbar"
        "homepage ads";
    grid-template-columns: 4fr 1.5fr;

    .navbar {
        grid-area: navbar;
    }
    .homepage {
        grid-area: homepage;
        margin-top: 1.5rem;
        margin-right: 8rem;
    }

    .ads {
        position: relative;
        grid-area: ads;
        width: 100%;
        height: 80%;
        margin-top: 3.2rem;
        border: 1px solid #d4d4d4;
        border-radius: 7px;
        overflow: scroll;
        -ms-overflow-style: none;
        scrollbar-width: none;
        &::-webkit-scrollbar {
            display: none;
        }
        .ads__title {
            text-align: center;
            margin-top: 0.1rem;
        }
    }
}

@media screen and (max-width: 786px) {
    #APP {
        margin: 0.5rem 0.5rem 0;
        grid-template-areas:
            "navbar"
            "homepage"
            "ads";
        grid-template-columns: 1fr;

        .navbar {
            margin: 0 1rem 0 0.2rem;
        }

        .homepage {
            margin: inherit;
            margin-top: 2rem;
        }
        .ads {
            margin-top: 2rem;
            height: 20rem;
        }
    }
}
